.h-plus {
  --v-h-plus-padding: 1.3333rem;
  --v-h-plus-gap: 2rem;
  --v-h-plus-item: 1rem;
  --v-h-plus-title-gap: 1.3333rem;
  display: flex;
  gap: var(--v-h-plus-gap);
  flex-wrap: wrap;
}
@media screen and (min-width: 768px) {
  .h-plus {
    flex-direction: column;
  }
}
.h-plus__item {
  display: flex;
  flex-direction: column;
  padding: var(--v-h-plus-padding);
  gap: var(--v-h-plus-item);
  flex-grow: 1;
  background-color: var(--v-black);
}
.h-plus__title {
  display: flex;
  justify-content: space-between;
  gap: var(--v-h-plus-title-gap);
  line-height: 1.2;
}
.h-plus__text {
  color: var(--v-secondary);
  line-height: 1.3;
}
.h-plus__icon {
  font-size: 1.3333rem;
  max-width: 20px;
}